@import "reset";
@import "fonts";
@import "mixins";
@import "vars";
@import "generated/sprite-svg";
:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
  }
}

body,
html {
  width: 100%;
  height: 100%;
}

.wrapper {
  width: 100%;
  // min-height: 100%;
  overflow: hidden;
  font-family: $font-primary;
  .icon {
    fill: $grey;
    transition: all .2s ease;
  }
}

.main {
  position: relative;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 6px;
    -webkit-box-shadow: inset 0px 3px 6px 0px rgba(0, 0, 0, 0.16);
    -moz-box-shadow: inset 0px 3px 6px 0px rgba(0, 0, 0, 0.16);
    box-shadow: inset 0px 3px 6px 0px rgba(0, 0, 0, 0.16);
    z-index: 10;
  }
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 1%;
  padding-right: 1%;
  box-sizing: content-box;
}

a {
  transition: all 0.3s ease;
  color: inherit;
}

// common icons begin
.add-plus {
  position: absolute;
  top: 44%;
  right: -46px;
  z-index: 10;
  @include rfs(65);
  width: 1em;
  height: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #fff;
  background-color: $silver;
  border-radius: 50%;
  -webkit-box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.36);
  -moz-box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.36);
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.36);
  .icon {
    @include rfs(36.41);
    fill: #fff;
  }
}

.plan__icons-box {
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.plan__icon {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $brown-rust;
  border-radius: 50%;
}

.icon-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 34px;
  padding-left: 1px;
  border: 1.5px solid #F0CDB0;
  border-radius: 50%;
  .icon-sign-in {
    font-size: 18px;
    fill: #F0CDB0;
  }
}

// common icons end
// header
.header {
  max-width: 1920px;
  margin: 0 auto;
  padding: 20px 10% 20px 3.5%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-height: 116px;
  .header__logo {
    width: 107px;
    img {
      width: 100%;
    }
  }
  .header__nav {
    display: flex;
    flex-grow: 1;
    justify-content: flex-end;
    @media only screen and (max-width: 992px) {
      margin-top: 20px;
      flex-direction: column;
      order: 2;
      flex: 0 0 100%;
    }
  }
  .header__nav--contact {
    margin-left: 10vw;
    @media only screen and (max-width: 1080px) {
      margin-left: 0;
    }
  }
  .header__nav-link {
    margin-right: 43px;
    @include rfs(14);
    color: $grey;
    text-transform: uppercase;
    font-weight: 500;
    transition: all .2s ease;
    @media only screen and (max-width: 992px) {
      margin-bottom: 5px;
    }
    &:hover {
      color: darken($green, 5%);
    }
  }
  .header__nav-link.is-active {
    color: $green;
  }
  .header__icons {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .icon-user {
    font-size: 25px;
    &:hover {
      fill: $black-text;
    }
  }
  .header__sign-in {
    margin-left: 13px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    min-width: 30px;
    min-height: 30px;
    background-color: $green;
    border-radius: 50%;
  }
  .icon-sign-in {
    font-size: 16px;
    fill: #fff;
    &:hover {
      fill: $black-text;
    }
  }
}

// FRONT PAGE
.frontpage {
  @include rfs(20);
  // common styles begin
  .text-title {
    margin-bottom: 20px;
    @include rfs(40);
    font-weight: 500;
  }
  .common-text {
    @include rfs(20);
  }
  .light-section {
    color: $black-text;
    background-color: #F8F8F8;
    padding-top: 50px;
    padding-bottom: 50px;
  }
  .question {
    display: flex;
    align-items: flex-start;
    @include rfs(75);
    line-height: 0.9;
  }
  .question-words {
    display: flex;
    flex-direction: column;
  }
  .quest-icon-box {
    width: 1.1em;
    height: 1.1em;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 50%;
    .icon-question {
      @include rfs(60);
      fill: $green;
    }
  }
  .container-wide {
    max-width: 1680px;
    margin: 0 auto;
    padding-left: 1%;
    padding-right: 1%;
  }
  // common styles end
  .green {
    position: relative;
    display: flex;
    @media only screen and (max-width: 1200px) {
      flex-wrap: wrap;
    }
  }
  .green__text {
    padding-left: 4.5%;
    padding-right: 4.5%;
    padding-top: 40px;
    padding-bottom: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include rfs(20);
    color: #fff;
    font-weight: 400;
    background-color: $green;
    @media only screen and (max-width: 1200px) {
      align-items: center;
    }
  }
  .green__question {
    margin-bottom: 20px;
  }
  .green__img {
    position: relative;
    flex: 0 0 66.3%;
    @media only screen and (max-width: 1200px) {
      flex: auto;
    }
    img {
      display: block;
      width: 100%;
    }
  }
  .green__img-docket {
    max-width: 467px;
    padding: 22px 30px;
    position: absolute;
    bottom: 16%;
    left: 4%;
    @include rfs(24);
    line-height: 1.16;
    color: #fff;
    background-color: rgba(137, 201, 102, 0.7);
    border-radius: 5px;
    @media only screen and (max-width: 1080px) {
      @include rfs(16);
    }
    @media only screen and (max-width: 640px) {
      position: static;
      max-width: 100%;
      border-radius: 0;
    }
    .green__img-docket-title {
      display: block;
      @include rfs(31);
      margin-bottom: 5px;
      @media only screen and (max-width: 1080px) {
        @include rfs(20);
      }
    }
  }
  .green--1 {
    .green__text {
      justify-content: space-around;
    }
  }
  .green--2 {
    @media only screen and (max-width: 1200px) {
      .green__text {
        order: 2;
      }
    }
    .green__img-docket {
      top: 1.5%;
      left: 1%;
      @media only screen and (min-width: 640px) {
        background: none;
      }
      span {
        margin-bottom: 0;
        padding-right: 60px;
        padding-left: 10px;
        display: inline-block;
        @include rfs(39);
        line-height: 1;
        @media only screen and (min-width: 640px) {
          background-color: rgba(137, 201, 102, 0.7);
        }
        &:first-child {
          padding-top: 10px;
          border-top-left-radius: 5px;
          border-top-right-radius: 5px;
        }
        &:last-child {
          padding-bottom: 10px;
          border-top-right-radius: 5px;
          border-bottom-left-radius: 5px;
          border-bottom-right-radius: 5px;
        }
      }
    }
  }
  .green--3 {
    .green__text-title {
      margin-bottom: 46px;
    }
    .green__img-docket {
      @include rfs(19);
      top: 6%;
      left: 14%;
      bottom: auto;
      @media only screen and (min-width: 640px) {
        background: none;
      }
      @media only screen and (min-width: 1200px) and (max-width: 1500px) {
        left: 20%;
      }
      @media only screen and (max-width: 992px) {
        left: 18%;
      }
    }
    .green__img-docket-top,
    .green__img-docket-bottom {
      display: block;
      padding: 10px 28px 10px 10px;
      border-radius: 5px;
      @media only screen and (min-width: 640px) {
        background-color: rgba(137, 201, 102, 0.7);
      }
    }
    .green__img-docket-top {
      line-height: 1.3;
    }
    .green__img-docket-bottom {
      margin-top: 10px;
      @include rfs(20);
    }
    .green__img-progress {
      position: absolute;
      top: 7%;
      left: 4%;
      span {
        display: inline-block;
        position: absolute;
        top: 40%;
        left: 34%;
        margin: auto;
        font-size: 26px;
        color: #fff;
      }
    }
    .icon-progress {
      font-size: 120.52px;
      fill: $green;
    }
    .icon-progress-part {
      position: absolute;
      top: 0;
      left: 50%;
      font-size: 38px;
      fill: #fff;
    }
  }
  // impact section
  .impact {
    font-weight: 500;
  }
  .impact__title {
    margin-bottom: 50px;
    @include rfs(30);
    text-align: center;
  }
  .impact__items-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    text-align: center;
    color: $green;
  }
  .impact__item {
    max-width: 300px;
    min-width: 200px;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    margin-bottom: 50px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .impact__quantity {
    @include rfs(35);
  }
  .impact__chevrons-box {
    margin-top: 15px;
    margin-bottom: 35px;
    @media only screen and (max-width: 992px) {
      margin-bottom: 20px;
    }
  }
  .impact__chevron-up {
    display: block;
    width: 0;
    height: 0;
    border-left: 23px solid transparent;
    border-right: 23px solid transparent;
    border-bottom: 15px solid $green;
    &:nth-child(2) {
      opacity: 0.6;
    }
    &:nth-child(3) {
      opacity: 0.2;
    }
  }
  .impact__icons {
    position: relative;
    margin-bottom: 43px;
    display: flex;
    align-items: center;
    @media only screen and (max-width: 1080px) {
      margin-bottom: 20px;
    }
    .icon {
      fill: $green;
    }
    .icon-money {
      @include rfs(60);
    }
    .icon-instagam {
      display: inline-block;
      width: 32px;
      height: 32px;
      margin-left: 10px;
    }
    .icon-hand {
      @include rfs(64);
    }
    .icon-landscape {
      @include rfs(37);
      fill: #fff;
    }
    .icon-arrows {
      @include rfs(72);
    }
    .icon-twitter {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      margin: auto;
      @include rfs(23);
      fill: #20B8FF;
    }
  }
  .impact__i-landscape {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 66.52px;
    height: 66.2px;
    background-color: #3B5998;
  }
  .impact__item-text {
    color: $black-text;
  }
  // story section
  .story__inner {
    padding-top: 30px;
    padding-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    @media only screen and (max-width: 1200px) {
      flex-flow: column;
    }
  }
  .story__text {
    max-width: 575px;
    @media only screen and (max-width: 1200px) {
      max-width: 1200px;
      margin-top: 40px;
      order: 2;
    }
  }
  .story__text-title {
    @include rfs(46);
  }
  .story__graphics {
    display: flex;
    flex-direction: column;
    align-items: center;
    .icon {
      fill: $green;
    }
  }
  .story__graphics-title {
    margin-bottom: 27px;
    @include rfs(23);
  }
  .story__graphics-desc {
    font-size: 15px;
  }
  .icon-storytelling {
    font-size: 59px;
    margin-bottom: 5px;
  }
  .icon-rack-wrap {
    padding-left: 9.5%;
    padding-right: 9.5%;
    margin-left: 7%;
    margin-right: 7%;
    .impact__chevrons-box {
      display: none;
      @media only screen and (max-width: 600px) {
        display: block;
      }
    }
  }
  .icon-rack {
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 465px;
    margin-top: 30px;
    margin-bottom: 20px;
    border-top: 1px solid $black-text;
    @media only screen and (max-width: 600px) {
      display: none;
    }
    span {
      display: inline-block;
      height: 15px;
      border-left: 1px solid $black-text;
    }
    .icon-rack__line {
      display: inline-block;
      position: absolute;
      top: -15px;
      width: 2px;
      left: 0;
      right: 0;
      margin: auto;
      border-left: 1px solid $black-text;
      z-index: 1;
    }
  }
  .story__graphics-items {
    width: 100%;
    display: flex;
    justify-content: space-between;
    text-align: center;
    @media only screen and (max-width: 600px) {
      flex-direction: column;
      align-items: center;
      .icon-plus {
        margin-top: 30px;
        margin-bottom: 30px;
      }
    }
  }
  .story__graphics-item {
    .story__graphics-desc {
      width: 110px;
      margin-top: 20px;
      @media only screen and (max-width: 600px) {
        margin-top: 5px;
      }
    }
  }
  // social section
  .social {
    color: $green;
    @media only screen and (min-width: 1200px) {
      padding-top: 120px;
      padding-bottom: 120px;
    }
  }
  .social__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media only screen and (max-width: 992px) {
      flex-direction: column;
    }
    @media only screen and (max-width: 480px) {
      text-align: center;
    }
  }
  .social__article {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    max-width: 867px;
    @media only screen and (max-width: 992px) {
      margin-top: 30px;
      order: 2;
    }
  }
  .social__ask {
    padding-top: 60px;
    @include rfs(29);
  }
  .social__picture {
    flex-basis: 41.5%;
    img {
      width: 100%;
    }
    @media only screen and (max-width: 992px) {
      flex-basis: 70%;
    }
  }
  // map section
  .map {
    position: relative;
    @media only screen and (max-width: 768px) {
      display: flex;
      flex-direction: column;
    }
  }
  #map {
    width: 100%;
    height: 527px;
    // background-image: url("../img/map.jpg");
    background-size: cover;
  }
  .map__article {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    max-width: 47%;
    padding-left: 7%;
    padding-right: 3%;
    color: $dark-grey;
    background-color: rgba(255, 255, 255, 0.6);
    @media only screen and (max-width: 1500px) {
      max-width: 60%;
      padding-left: 2%;
      padding-right: 2%;
    }
    @media only screen and (min-width: 768px) {
      height: 527px;
    }
    @media only screen and (max-width: 768px) {
      max-width: 100%;
      position: static;
      padding-top: 40px;
      padding-bottom: 40px;
    }
  }
  .map__text {
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  // hashtag section
  .hashtag {
    padding-top: 105px;
    padding-bottom: 105px;
    background-color: $grey;
    color: #fff;
    background-size: cover;
    background-position-x: center;
    @media only screen and (max-width: 768px) {
      text-align: center;
      background-image: url("../img/sunset@768.jpg")!important;
    }
    @media only screen and (max-width: 480px) {
      background-image: url("../img/sunset@480.jpg")!important;
    }
  }
  .hashtag__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    @media only screen and (max-width: 1200px) {
      flex-direction: column;
    }
  }
  .hashtag__article {
    max-width: 591px;
    padding-right: 50px;
    @media only screen and (max-width: 1200px) {
      max-width: 1200px;
      order: 2;
      padding-right: 0;
    }
  }
  .hashtag__title {
    margin-bottom: 40px;
  }
  .hashtag__cloud-tags {
    max-width: 1084px;
    flex-basis: 65%;
    @media only screen and (max-width: 1200px) {
      width: 100%;
      margin-bottom: 40px;
    }
    img {
      width: 100%;
    }
  }
  .hashtag__cycle {
    margin-top: 130px;
    max-width: 371px;
    @media only screen and (max-width: 768px) {
      margin: 0 auto;
      margin-top: 60px;
    }
  }
  // why section
  .why {
    padding-top: 80px;
    padding-bottom: 55px;
    background-color: $dark-grey;
    color: $green;
    .why__header {
      display: flex;
      justify-content: space-between;
      @media only screen and (max-width: 768px) {
        flex-direction: column;
        align-items: center;
      }
    }
    .question {
      @include rfs(87);
    }
    .common-text {
      @include rfs(25);
    }
    .quest-icon-box {
      background-color: $green;
    }
    .icon-question {
      fill: $dark-grey;
    }
  }
  .why__citation {
    margin-left: 20px;
    color: #fff;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    line-height: 1.3;
    @media only screen and (max-width: 768px) {
      margin-top: 20px;
      margin-left: 0;
    }
  }
  .why__quote {
    max-width: 1088px;
    display: flex;
    @media only screen and (min-width: 992px) {
      padding-right: 13%;
      box-sizing: content-box;
    }
  }
  .why__left {
    padding-bottom: 10px;
    margin-right: 10px;
  }
  .icon-quote2 {
    fill: #fff;
    @include rfs(16.3);
    &:last-child {
      margin-left: 40px;
      padding-bottom: 0;
      @media only screen and (max-width: 768px) {
        margin-left: 0px;
      }
    }
  }
  .why__citation-author {
    align-self: flex-end;
    @include rfs(18);
  }
  .why__columns {
    margin-top: 65px;
    display: flex;
    justify-content: space-between;
    @media only screen and (max-width: 1500px) {
      flex-wrap: wrap;
    }
    @media only screen and (max-width: 1366px) {
      flex-direction: column;
      margin-bottom: 30px;
    }
  }
  .why__item {
    margin-top: 30px;
    min-width: 237px;
    max-width: 313px;
    margin-left: 3%;
    @media only screen and (max-width: 1500px) {
      margin-left: 0;
    }
    @media only screen and (max-width: 1366px) {
      max-width: 1366px;
      flex-basis: 100%;
    }
    &:first-child {
      margin-left: 0;
      min-width: 333px;
    }
  }
  .why__item-title {
    margin-bottom: 28px;
    @include rfs(25);
    min-height: 60px;
    @media only screen and (max-width: 1500px) {
      margin-bottom: 10px;
      min-height: 0;
      font-weight: 500;
    }
  }
  .why__item-text {
    margin-bottom: 25px;
    @include rfs(18);
    line-height: 1.2;
    @media only screen and (max-width: 1366px) {
      margin-bottom: 0px;
    }
  }
  // phone-screenshot section
  .phone-screenshot {
    .phone-screenshot__img {
      width: 100%;
      display: block;
    }
  }
  // pacitipate section
  .paticipate {
    padding-top: 80px;
    padding-bottom: 80px;
    color: #fff;
    background-color: $dark-blue;
    .question {
      margin-bottom: 90px;
      @include rfs(81);
      color: $green;
      @media only screen and (max-width: 480px) {
        margin-bottom: 50px;
      }
    }
    .common-text {
      margin-left: 5px;
      @include rfs(26);
    }
    .quest-icon-box {
      background-color: $green;
    }
    .icon-question {
      fill: $dark-blue;
    }
  }
  .paticipate__columns {
    display: flex;
    justify-content: space-between;
    @media only screen and (max-width: 1366px) {
      flex-wrap: wrap;
      justify-content: space-around;
    }
  }
  .paticipate__item {
    max-width: 244px;
    @media only screen and (max-width: 480px) {
      max-width: 90%;
    }
    .icon {
      fill: $green;
      font-size: 39px;
    }
  }
  .paticipate__item-head {
    display: flex;
    margin-bottom: 50px;
    img {
      display: inline-block;
      height: 1em;
    }
    @media only screen and (max-width: 480px) {
      margin-bottom: 20px;
    }
  }
  .icon-monitoring {
    height: 39px;
  }
  .paticipate__item-caption {
    max-height: 39px;
    margin-left: 10px;
    padding-left: 4px;
    padding-top: 4px;
    flex-grow: 1;
    display: flex;
    align-items: center;
    color: $dark-blue;
    border: 1px solud #95989A;
    background-color: $green;
    .icon {
      @include rfs(35);
      fill: $dark-blue;
    }
  }
