      margin: 0;
    }
  }
  .video__content-sticker--1 {
    margin-right: 20px;
    display: flex;
    align-items: flex-end;
    .chevron {
      top: 30px;
      left: -22px;
      transform: rotate(-90deg);
      @media only screen and (max-width: 768px) {
        top: auto;
        bottom: -15px;
        left: 30px;
        transform: rotate(-180deg);
      }
    }
  }
  .video__content-sticker--2 {
    text-align: right;
    .chevron {
      top: -15px;
      left: 30px;
    }
  }
  // FOOTER
  .footer {
    padding-top: 50px;
    @include rfs(12);
    color: $grey;
    background-color: $light-grey;
    .container {
      max-width: 1160px;
