/***************************
****************************
       FEATURES PAGES
****************************
***************************/

.features {
  .container {
    max-width: 1140px;
  }
  .section__title {
    min-height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    @include rfs(30);
    color: #fff;
    font-weight: 700;
    text-transform: uppercase;
    background-color: $green;
  }
  .section__content {
    background-repeat: no-repeat;
    background-size: cover;
    background-position-x: center;
  }
  .bg-shade {
    padding-bottom: 140px;
    background-color: rgba(0, 0, 0, 0.45);
  }
  .plans-wrap {
    .container {
      padding-top: 95px;
      display: flex;
      justify-content: space-between;
      color: $mine-shaft;
      @media only screen and (max-width: 992px) {
        flex-direction: column;
        align-items: center;
        .add-plus {
          display: none;
        }
      }
    }
  }
  .plans__item {
    max-width: 400px;
    position: relative;
    flex-basis: 33.33%;
    margin-bottom: 120px;
    padding: 35px 30px 130px 30px;
    border: 2px solid $bronze;
    border-radius: 3px;
    background-color: #EDEDED;
    transition: background-color .3s ease;
    -webkit-box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.23);
    -moz-box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.23);
    box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.23);
    @media only screen and (min-width: 992px) {
      margin-left: 24px;
    }
    &:hover {
      background-color: #fff;
    }
    &:first-child {
      margin-left: 0;
    }
  }
  .plans__item--bronze {
    .icon-label {
      fill: $green;
    }
    .plan__label-text {
      font-size: 16px;
    }
  }
  .plans__item--silver {
    border-color: $silver;
    .plan__label-text {
      top: 40px;
      right: 2px;
    }
    .plan__price {
      border-color: $silver;
    }
    .add-plus {
      background-color: $laser;
    }
  }
  .plans__item--silver,
  .plans__item--gold {
    .plan__icon {
      &:first-child {
        margin-right: -25px;
        transform: scale(0.8);
      }
      &:nth-child(2) {
        background-color: $silver;
        z-index: 2;
        .icon-wrap {
          border-color: $light-grey;
        }
        .icon-sign-in {
          fill: $light-grey;
        }
      }
    }
  }
  .plans__item--gold {
    border-color: $laser;
    .plan__icon {
      &:first-child {
        margin-right: -30px;
      }
      &:nth-child(2) {
        margin-right: -25px;
        transform: scale(0.8);
      }
      &:nth-child(3) {
        background-color: $laser;
        z-index: 3;
        .icon-wrap {
          border-color: #FFE3B1;
        }
        .icon-sign-in {
          fill: #FFE3B1;
        }
      }
    }
    .plan__label-text {
      top: 42px;
      right: -4px;
    }
    &:first-child {
      transform: scale(0.8);
    }
    .plan__price {
      border-color: $laser;
      .icon-dollar {
        margin-right: -4px;
      }
    }
  }
  .plan__head {
    max-width: 235px;
    margin: 0 auto;
    text-align: center;
  }
  .plan__label {
    position: absolute;
    font-size: 126.44px;
    top: -6px;
    right: -7px;
  }
  .icon-label {
    fill: $dodger-blue;
  }
  .plan__label-text {
    position: absolute;
    top: 40px;
    right: 2px;
    top: 38px;
    right: 4px;
    color: #fff;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    transform: rotate(45deg);
  }
  .plan__title {
    @include rfs(21);
    font-weight: 900;
    text-transform: uppercase;
  }
  .plan__time {
    max-width: 235px;
    margin-top: 20px;
    margin-bottom: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      flex-grow: 1;
      height: 1px;
      background-color: $mine-shaft;
      box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.26);
    }
  }
  .plan__time-title {
    padding-left: 10px;
    padding-right: 10px;
    @include rfs(12);
    color: $mine-shaft;
    text-transform: uppercase;
    font-weight: 700;
  }
  .plan__list {
    // max-width: 260px;
    padding-left: 30px;
  }
  .plan__list-item {
    position: relative;
    // margin-bottom: 5px;
    @include rfs(13);
    line-height: 1.6;
    .icon-check {
      position: absolute;
      left: -25px;
      top: 4px;
      @include rfs(10);
      fill: $green;
    }
  }
  .plan__price {
    position: absolute;
    bottom: -1.1em;
    right: 0;
    left: 0;
    margin: auto;
    padding-top: 0.3em;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    @include rfs(69);
    height: 2.15em;
    width: 2.15em;
    color: $green;
    line-height: 22px;
    border-radius: 50%;
    background-color: #fff;
    border: 2px solid $bronze;
    -webkit-box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.2);
    .icon-dollar {
      @include rfs(40.27);
      fill: $green;
    }
  }
  .plan__price-number {
    display: flex;
    align-items: baseline;
  }
  .plan__price-per {
    @include rfs(14);
    letter-spacing: 0.5;
    font-weight: 500;
  }
  // cards
  .cards-wrap {
    margin-bottom: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #f0f0f0;
    @media only screen and (max-width: 992px) {
      flex-direction: column;
      align-items: center;
      background-color: transparent;
    }
  }
  .cards__item {
    flex-basis: 33.33%;
    padding: 60px 40px 60px;
    @include rfs(15);
    text-align: center;
    background-color: #fff;
    border-left: 1px solid #95989A;
    border-right: 1px solid #95989A;
    box-sizing: border-box;
    &:first-child {
      flex-basis: calc(33.33% + 3px);
      margin-left: calc(33.33% - 1px);
      margin-right: calc(33.33% - 2px);
      padding-top: 65px;
      @media only screen and (max-width: 992px) {
        margin-left: 0;
        margin-right: 0;
      }
    }
    &:nth-child(2) {
      border-left-color: transparent;
    }
    &:nth-child(3) {
      border-right-color: transparent;
    }
    @media only screen and (max-width: 992px) {
      max-width: 480px;
      margin-bottom: 50px;
      border-radius: 3px;
    }
  }
  .card__title {
    color: $grey;
  }
  .card__desc {
    margin-top: 20px;
    margin-bottom: 55px;
    color: $dark-grey;
    line-height: 21px;
    text-align: left;
  }
  .card__button {
    padding: 13px 25px;
    border: 2px solid $green;
    color: $green;
    text-transform: uppercase;
    font-weight: 700;
    border-radius: 2px;
  }
  .section__compare {
    min-height: 60px;
    @include rfs(20);
    border-radius: 3px;
    -webkit-box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.63);
    -moz-box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.63);
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.63);
    &:hover {
      background-color: darken($green, 5%)
    }
  }
  // table section
  .section-table {
    background-color: #fafafa;
  }
  .tables-wrap {
    padding-top: 75px;
    padding-bottom: 40px;
  }
  .table {
    width: 100%;
    margin-bottom: 75px;
    border-collapse: collapse;
    color: $dark-grey;
    td {
      border: 1px solid $green;
      padding-top: 10px;
      padding-bottom: 10px;
      font-size: 15px;
      font-weight: 500;
      background-color: #fff;
      line-height: 20px;
    }
    thead th {
      background-color: $green;
    }
    th {
      border: 1px solid $green;
      background-color: #fafafa;
    }
  }
  .table__head {
    display: block;
    padding-left: 30px;
    width: 100%;
    padding-top: 12px;
    padding-bottom: 12px;
    @include rfs(15);
    color: #fff;
    text-align: left;
    text-transform: uppercase;
    font-weight: 900;
    border: 1px solid $green;
    background-color: $green;
  }
  .table__left-ceil {
    padding-left: 30px;
    @media only screen and (max-width: 480px) {
      padding-left: 5px;
    }
  }
  .table__right-ceil {
    display: block;
    padding-left: 12%;
    padding-right: 12%;
    text-align: center;
    color: $green;
    font-weight: 900;
  }
  /***************************
****************************
        COMPARE HTML
****************************
***************************/
