/***************************
****************************
      TARIFFS PAGES
****************************
***************************/

.tariff-wrap {
  color: $grey;
  .container {
    max-width: 1140px;
  }
  .tariff__section {}
  .add-plus {
    position: absolute;
    top: auto;
    bottom: -0.5em;
    left: 27%;
    background-color: $brown-rust;
    @include rfs(45);
    z-index: 10;
    .icon {
      @include rfs(25);
    }
    @media only screen and (max-width: 768px) {
      left: 48%;
      .icon {
        @include rfs(18);
      }
    }
  }
  .tariff__title {
    padding-top: 65px;
    display: flex;
    align-items: center;
    @include rfs(20);
    font-weight: 700;
    text-transform: uppercase;
  }
  .plan__icons-box {
    margin-bottom: 0;
    margin-right: 15px;
  }
  .tariff {
    position: relative;
    padding-top: 60px;
    padding-bottom: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media only screen and (max-width: 768px) {
      flex-direction: column;
    }
  }
  .tariff__picture {
    flex-basis: 54%;
    -webkit-box-shadow: 5px 5px 50px 0px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 5px 5px 50px 0px rgba(0, 0, 0, 0.2);
    box-shadow: 5px 5px 50px 0px rgba(0, 0, 0, 0.2);
    @media only screen and (max-width: 768px) {
      margin-bottom: 50px;
    }
    img {
      display: block;
      width: 100%;
    }
  }
  .tariff__article {
    flex-basis: 39%;
  }
  .article__title {
    margin-bottom: 25px;
    @include rfs(25);
    font-weight: 700;
  }
  .article__small-title {
    margin-top: 25px;
    margin-bottom: 13px;
    @include rfs(15);
    font-weight: 700;
  }
  .article__paragraph {
    font-size: 12px;
    line-height: 1.4;
    font-weight: 500;
  }
  .tariff__section--dark {
    background-color: #fafafa;
    background-image: url("../img/cube-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
  }
  .tariff--stretched {
    align-items: stretch;
    .tariff__article {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
      .btn-green {
        margin: 0 auto;
        margin-top: 30px;
      }
    }
  }
  .btn-green {
    padding: 9px 30px 8px 15px;
    display: flex;
    align-items: center;
    @include rfs(15);
    color: #fff;
    font-weight: 900;
    background-color: $green;
    border-radius: 3px;
    &:hover {
      background-color: darken($green, 10%);
    }
  }
  .plan__icon {
    width: 30px;
    height: 30px;
  }
  .icon-wrap {
    width: 26px;
    height: 26px;
    border: 1px solid #F0CDB0;
    .icon-sign-in {
      font-size: 14px;
    }
  }
  // tariff  footer
  .footer {
    padding-top: 60px;
    padding-bottom: 105px;
    color: #fff;
    background-color: $green;
    background-image: url("../img/green-cube-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    .add-plus {
      position: static;
      margin: 0 auto;
    }
    .icon-here {
      display: block;
      height: 1em;
      @include rfs(56.87);
    }
  }
  .footer__title {
    margin-top: 30px;
    margin-bottom: 30px;
    @include rfs(35);
    font-weight: 700;
    text-align: center;
  }
  .footer__article {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media only screen and (max-width: 992px) {
      justify-content: space-around;
    }
    .icon {
      fill: #fff;
    }
    .icon-hash {
      @include rfs(24.4);
    }
    .icon-users {
      @include rfs(48.4);
    }
    .icon-eye {
      @include rfs(36.43);
    }
    .icon-sindication {
      @include rfs(66.24);
    }
    .icon-reporting {
      @include rfs(29.59);
    }
    .icon-graphic {
      @include rfs(53.78);
    }
    .icon-handshaking {
      @include rfs(40.88);
    }
  }
  .article__item {
    margin-top: 5px;
    margin-left: 5%;
    flex-basis: 20%;
    text-align: center;
    &:first-child,
    &:nth-child(5) {
      margin-left: 0;
    }
    @media only screen and (min-width: 992px) {
      &:nth-child(4) {
        flex-basis: 22%;
      }
    }
    @media only screen and (max-width: 992px) {
      margin-left: 0;
      flex-basis: 30%;
    }
    @media only screen and (max-width: 768px) {
      flex-basis: 40%;
    }
    @media only screen and (max-width: 480px) {
      flex-basis: 90%;
      margin-top: 0;
    }
  }
  .article__icon {
    min-height: 6em;
    @include rfs(15);
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
}

// TARIFF SILVER
.tariff--silver {
  .plan__icons-box--silver {
    margin-left: -27px;
    .icon-wrap {
      border-color: $light-grey;
    }
    .icon-sign-in {
      fill: $light-grey;
    }
    .plan__icon {
      background-color: $silver;
    }
  }
  .add-plus {
    background-color: $silver;
  }
  .article__figure {
    display: flex;
  }
  .figure {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  .figure__icons {
    margin-right: 8px;
    display: flex;
    align-items: center;
    .icon-plus {
      margin-right: 7px;
      font-size: 14.63px;
    }
    .plan__icons-box {
      margin-right: 7px;
    }
  }
  .figure__link {
    margin-top: 15px;
    color: $green;
    text-decoration: underline;
    font-style: italic;
    font-weight: 700;
  }
  .figure-row {
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    margin-top: 15px;
  }
  .icon-word-logo {
    font-size: 30.62px;
    fill: #d8d8d8;
  }
  .logo-grey {
    width: 30px;
    height: 30px;
    margin-left: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $silver;
    .icon-word-logo {
      font-size: 10.49px;
      fill: #fff;
    }
  }
  .icon-spectrum {
    margin-right: 15px;
    width: 45px;
    height: 45px;
    img {
      width: 33.75px;
    }
  }
  .icon-block {
    margin-left: 7px;
    width: 30px;
    height: 30px;
    background-color: #B29C72;
  }
  .icon-block--blue {
    background-color: #3030CE;
  }
  .brush {
    width: 25px;
    height: 25px;
    margin-left: 7px;
  }
  .btn-book {
    height: 30px;
    line-height: 30px;
    margin-left: 20px;
    padding-left: 17px;
    padding-right: 15px;
    font-size: 12px;
    color: #fff;
    font-weight: 700;
    white-space: nowrap;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    background-color: #D88E25;
    &:hover {
      background-color: darken(#D88E25, 5%)
    }
  }
  .icon-shadow {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: #fff;
    -webkit-box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.2);
  }
  .btn-box {
    margin-top: 18%;
    display: inline-block;
  }
}


/***************************
****************************
      CONTACT PAGE
****************************
***************************/

.contact {
  min-height: calc(100vh - 116px);
  background-color: #fafafa;
  .container {
    max-width: 1140px;
  }
  .contact-inner {
    padding-top: 85px;
    padding-bottom: 85px;
    display: flex;
    justify-content: space-between;
    @media only screen and (max-width: 768px) {
      flex-direction: column;
      align-items: center;
    }
  }
  .contact__item {
    margin-bottom: 50px;
    flex-basis: 30%;
    min-height: 509px;
    padding: 70px 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    border-radius: 3px;
    -webkit-box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.16);
    -moz-box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.16);
    box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.16);
    @media only screen and (max-width: 768px) {
      flex-basis: 100%;
      max-width: 480px;
      width: 90%;
    }
  }
  .contact__icon {
    margin-bottom: 20px;
    .icon {
      fill: $green;
    }
    .icon-address {
      @include rfs(50.27);
    }
    .icon-phone {
      @include rfs(45.57);
    }
    .icon-email {
      @include rfs(35.02);
    }
  }
  .contact__item-name {
    color: $green;
    @include rfs(15);
    text-transform: uppercase;
    font-weight: 900;
  }
}